
body {
  
  margin: 0;
  padding: 0;
  font-family: verdana, sans-serif;
  color: #888;
  background-color: #333;
  
}

a {
	text-decoration: none;
	color: lightgrey;
}

input {
	background: #444;
	border: none;
	padding: 10px;
	border-radius: 5px;
	border-bottom: 1px solid #666;
	
}

li a {
	text-decoration: none;
	color: #999;
}

.source {
	color: grey;
	font-size: .8rem;
	
}

.time {
	border: 1px solid #444;
	padding: 3px;
	color: lightgrey;
	border-radius: 3px;
	font-size: .8rem;
}


.container{
	display: -webkit-flex;
	display: flex;
	height: 93vh;
	border-top: solid 1px #444;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
}


.prices{
	padding: 10px;
	-webkit-flex: 1 1;
	        flex: 1 1;
	border-right: solid 1px #444;
	overflow-y: scroll;
	height: 92vh;
}

.news{
	
	padding: 10px;
	-webkit-flex: 3 1;
	        flex: 3 1;
	overflow-y: scroll;
	min-width: 400px;
	height: 92vh;
	
}

.crumbs{
	padding: 10px;
	-webkit-flex: 2 1;
	        flex: 2 1;
	overflow-y: scroll;
	border-left: solid 1px #444;	
	background-color: #444;
	height: 92vh;
}

.title {
	padding: 10px;
	color: gold;
}

.title img {

  -webkit-animation: App-logo-spin infinite 20s linear;

          animation: App-logo-spin infinite 20s linear;
  height: 20px;
  vertical-align: middle;
  margin-bottom: 3px;

}


@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.newsItem{
	border-bottom: solid 1px #444;
	padding-left: 10px;
	display: -webkit-flex;
	display: flex;
}

.newsItemRight{
	min-width: 100px;
	text-align: right;
	margin-left: auto;
}

.newsImage{
	width: 100px;
	max-height: 60px;
	margin: 5px;
	margin-right: 20px;
}

.hr {
	margin-top: 10px;
	margin-bottom: 10px;
	border-bottom: solid 1px #444;
}


button {
	padding: 10px;
	padding-left: 15px;
	padding-right: 15px;
	background-color: gold;
	opacity: .9;
	border: none;
	border-radius: 3px;
	cursor: pointer;
}

.box {
	background-color: #333;
	padding: 10px;
	padding-top: 20px;
	padding-bottom: 20px;
	margin-bottom: 10px;
	border-radius: 3px;

}

.carName{
	padding-left: 5px;
}
